<template>
	<div class="login-bg">
		<div class="login-box">

			<div class="login-logo">
				<img style="width: 175px;height: 120px;" src="../../common/img/icon_logo.png">
			</div>

			<div class="input-box">
				<i class="icon-username" @click="iconClick('username',$event)"></i>
				<input placeholder="请输入用户名" id="usernameIput" type="text" v-model="loginName" />

			</div>

			<div class="input-box">
				<i class="icon-password" @click="iconClick('password',$event)"></i>
				<input placeholder="请输入密码" id="passwordIput" type="password" v-model="loginPassword" />

			</div>

			<div class="loginbutonDiv">
				<el-button class="buttonSize button-solid" type="primary" @click="login">登录</el-button>
			</div>

		</div>
	</div>
</template>

<script>
	import { Message } from 'element-ui'

	export default {
		data() {
			return {
				loginName: '',
				loginPassword: ''
			}
		},
		methods: {
			iconClick(item){
				if(item == 'username'){
					document.getElementById('usernameIput').focus();
				}
				else{
					document.getElementById('passwordIput').focus();
				}


			},
			login() {
				let self = this;
        self.$router.push("/welcome");
				// if(!self.loginName) {
				// 	Message.error({
				// 		message: '请填写用户名'
				// 	})
				// 	return
				// }
				// if(!self.loginPassword) {
				// 	Message.error({
				// 		message: '请填写密码'
				// 	})
				// 	return
				// }
				// var p = {
				// 	operName: self.loginName,
				// 	operPassword: self.loginPassword
				// }
				// self.$api.post('user/noauth/login', p, r => {
				// 	var user = {
				// 		"name": r.repData.name,
				// 		"token": r.repData.token,
				// 		"userId": r.repData.userId
				// 	}
				// 	self.$api.sSetObject('_user', user);
				// 	self.$router.push("/welcome");
				// }, e => {

				// 	console.log(e);
				// })
			}
		},
		mounted() {
			var self = this
			document.onkeydown = function(e) {
				var ev = document.all ? window.event : e;
				if(ev.keyCode == 13) {
					self.login();
					return false;
				}
			}
		}
	}
</script>

<style scoped>
	.login-bg {
		width: 100%;
		height: 100%;
		background: url(../../common/img/icon_loginbg.png);
		background-attachment: fixed;
		background-repeat: no-repeat;
		/*占满屏幕,auto是随尺寸*/
		background-size: cover;
	}
	/*400*300*/

	.login-box {
		width: 360px;
		height: 360px;
		padding: 20px;
		position: fixed;
		border-radius: 5px;
		background-color: rgba(255, 255, 255, 0.3);
		margin: -200px 0 0 -200px;
		left: 50%;
		top: 50%;
	}

	.login-logo {
		width: 175px;
		height: 120px;
		margin: 20px auto 20px;
	}
	/*输入框*/

	.input-box {
		width: 320px;
		height: 40px;
		line-height: 40px;
		margin-bottom: 20px;
		background: white;
		margin: 0 auto 20px;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
	}

	.input-box>input {
		width: 85%;
		height: 100%;
		background: none;
		color: #666;
		font-size: 14px;
		line-height: inherit;
		padding-left: 15%;
		display: inline-block;
		/*去除谷歌输入框的黄色背景*/
		-webkit-box-shadow: 0 0 0px 1000px white inset;
		overflow: hidden;
	}

	.icon-username {
		position: absolute;
		width: 15%;
		height: 40px;
		background: url(../../common/img/icon_head.png) no-repeat center;
		display: inline-block;
	}

	.icon-password {
		position: absolute;
		width: 15%;
		height: 40px;
		background: url(../../common/img/icon_password.png) no-repeat center;
		display: inline-block;
	}

	.loginbutonDiv {
		text-align: center;
	}

	.buttonSize {
		width: 320px;
		height: 40px;
		border-radius: 6px;
	}
	/*实心*/

	.button-solid {
		background: #D9524F;
		color: white;
		border-color: #D9524F;
	}

	.button-solid.is-active,
	.button-solid:active {
		background: #D9524F;
		border-color: #D9524F;
		color: white;
	}

	.button-solid:focus,
	.button-solid:hover {
		background: #d85e5b;
		border-color: #d85e5b;
		color: white;
	}
</style>



